import React from 'react';

function DashboardRH({ user }) {
  const gestionSections = [
    { title: 'Grer les responsables formation', link: '/dashboard_responsable' },
    { title: 'Grer JobPost', link: '/dashboard' },
    { title: 'Grer les participants', link: '/Dashboardparticipant' },
    { title: 'Grer Employe', link: '/Dashboardemploye' },
    { title: 'Grer Fiche Employe', link: '/Dashboardfiche' },
    { title: 'Grer Formation', link: '/Dashboardformation' },
    { title: 'Grer Evaluation Chaud', link: '/DashboardEvaluationChaud' },
    { title: 'Grer Evaluation Froid', link: '/DashboardEvaluationFroid' },
    { title: 'Grer Comptences', link: '/gerer_competences' },
    { title: 'Grer Evaluation Comptences', link: '/evaluation_competences' },
    { title: 'Grer Adresse', link: '/gerer_adresse' },
    { title: 'Grer Dpartement', link: '/dashboard_department' },
  ];

  const sectionGroups = [];
  for (let i = 0; i < gestionSections.length; i += 3) {
    sectionGroups.push(gestionSections.slice(i, i + 3));
  }

  return (
    <div className="mt-5 pb-5">
      <div className="container">
        {sectionGroups.map((group, index) => (
          <div className="row mb-4" key={index}>
            {group.map((section, idx) => (
              <div className="col-md-4" key={idx}>
                <div className="card">
                  <div className="card-body">
                    <h4>{section.title}</h4>
                    <a href={section.link} className="btn btn-primary btn-lg">Accder</a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
}

export default DashboardRH;
